import axios from 'axios'

const urlpost = 'post/'
const urlcomment = 'comment/'

class postroutes {
    static getnew(pagenum) {
        return new Promise((resolve,reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlpost}new/${pagenum}`, headers: {Authorization: `Bearer ${token}`}, method: 'GET'}).then((result) => {
                resolve(
                    result.data.map(post =>({
                        ...post,
                        createdAt: new Date(post.createdAt)
                    })))
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
       
    }
    static getpopular(pagenum) {
        return new Promise((resolve,reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlpost}popular/${pagenum}`, headers: {Authorization: `Bearer ${token}`}, method: 'GET'}).then((result) => {
                resolve(result.data.map(post =>({
                    ...post,
                    createdAt: new Date(post.createdAt)
                })))
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }

    static createpost(title, body) {
        return new Promise((resolve,reject) => {
            console.log('a')
            const token = localStorage.getItem('token')
            axios({url: `${urlpost}createpost`, data: {title: title, postbody: body}, headers: {Authorization: `Bearer ${token}`}, method: 'POST'}).then((result) => {
                resolve(`${result.data}`)
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }

    static getpost(postId) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlpost}${postId}`, headers: {Authorization: `Bearer ${token}`}, method: 'GET'}).then((result) => {
                var post = result.data
                post.createdAt = new Date(post.createdAt)
                resolve(post)
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }

    static likepost(postId) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlpost}${postId}/like`, headers: {Authorization: `Bearer ${token}`}, method: 'PUT'}).then(() => {
                resolve('success')
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }

    static dislikepost(postId) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlpost}${postId}/dislike`, headers: {Authorization: `Bearer ${token}`}, method: 'PUT'}).then(() => {
                resolve('success')
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
       
    }

    static deletepost(postId) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlpost}${postId}`, headers: {Authorization: `Bearer ${token}`}, method: 'DELETE'}).then((result) => {
                resolve(result.data.message)
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
       
    }

    static getcomments(postId) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlcomment}allcommentspost/${postId}`, headers: {Authorization: `Bearer ${token}`}, method: 'GET'}).then((result) => {
                resolve(result.data.map(comment =>({
                    ...comment,
                    createdAt: new Date(comment.createdAt)
                })))
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }

    static postcomment(postId, comment) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlcomment}${postId}/addcomment`, data: {commentbody: comment}, headers: {Authorization: `Bearer ${token}`}, method: 'POST'}).then(() => {
                resolve('success')
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }

    static likecomment(commentId) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlcomment}${commentId}/like`, headers: {Authorization: `Bearer ${token}`}, method: 'PUT'}).then(() => {
                resolve('success')
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
       
    }

    static dislikecomment(commentId) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${urlcomment}${commentId}/dislike`, headers: {Authorization: `Bearer ${token}`}, method: 'PUT'}).then(() => {
                resolve('success')
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }

} 

export default postroutes