import dateago from './dateago.js'
import postroutes from './posts.js'

class displaycomments {
    static displaycomments(comment) {
    var newdiv = document.createElement('div')
    newdiv.className = 'commentcontainer'
    newdiv.id = `${comment._id}`
    
    var likebuttonsandnumdiv = document.createElement('div')
    likebuttonsandnumdiv.id = 'likebuttonsandnumdiv'
    
    var likenum = document.createElement('h5')
    likenum.id = 'likenum'
    likenum.innerHTML = `${comment.like_count}`
    
    var likebutton = document.createElement('button')
    likebutton.className = 'likebuttons'
    likebutton.id = `${comment._id}`
    likebutton.onclick = async function () {
        try {
            await postroutes.likecomment(comment._id)
        } catch (err) {
            console.log(err)
        }
    };

    var dislikebutton = document.createElement('button')
    dislikebutton.className = 'dislikebuttons'
    dislikebutton.id = `${comment._id}`
    dislikebutton.onclick = async function () {
        try {
            await postroutes.dislikecomment(comment._id)
        } catch (err) {
            console.log(err)
        }
    };

    likebuttonsandnumdiv.appendChild(likebutton)
    likebuttonsandnumdiv.appendChild(likenum)
    likebuttonsandnumdiv.appendChild(dislikebutton)

    var bodyandlabeldiv = document.createElement('div')
    bodyandlabeldiv.id = 'bodyandlabeldiv'
    
    var commentbody = document.createElement('textarea')
    commentbody.id = 'commentbody'
    commentbody.cols = '50'
    commentbody.rows = '2'
    commentbody.disabled = true
    commentbody.innerHTML = (`${comment.body}`)
    
    var userlabel = document.createElement('h5')
    userlabel.className = 'userlabel'
    
    var datetime = dateago.finddateago(comment.createdAt.getTime())
    userlabel.innerHTML = `Posted by <a href='/user/${comment.username}' style='text-decoration: none'>${comment.username}</a> ${datetime} ago`
    
    bodyandlabeldiv.appendChild(userlabel)
    bodyandlabeldiv.appendChild(commentbody)

    newdiv.appendChild(bodyandlabeldiv)
    newdiv.appendChild(likebuttonsandnumdiv)
    
    return newdiv
}
}

export default displaycomments
