class dateago{
    static finddateago(date1) {
        var date2 = Date.now()
        var differencetime = date2 - date1
        var finaltime = 0

        if(differencetime < 60000) {
            return `less than a minute`
        } else if(differencetime < 3600000) {
            finaltime = Math.round(differencetime/60000)
            return `${finaltime} minutes`
        } else if(differencetime < 86400000) {
            finaltime = Math.round(differencetime/3600000)
            return `${finaltime} hours`
        } else if(differencetime < 31536000000) {
            finaltime = Math.round(differencetime/86400000)
            return `${finaltime} days`
        } else {
            finaltime = Math.round(differencetime/31536000000)
            return `${finaltime} years`
        }
    }
}
    

export default dateago