<template>
    <div style="height: 100%; background-color: rgb(228, 225, 225);">
        <div id = 'home'>
            <div id = 'sortby'>
                <h5 style="margin: 0%; margin-top: 5px;">Sort By</h5>
            </div>
            <div id ='optionsholder'>
                <button class ="orderbutton" id="newbutton" v-on:click='displaynew'>New</button>
                <button class ="orderbutton" id="popbutton" v-on:click='displaypop'>Popular</button>
                <button id ="createpostbutton" v-on:click='createpostform'>Create Post</button>
            </div>
            
            <div id='overlay' style="display: none;">
                <div id='formholder'>
                    <form id='createpostbox'>
                        <h2 id="newpostlabel">Create a new post</h2>
                        <input type="text" id="createtitleinput" v-model="title" placeholder="Title" maxlength="100" required>
                        <textarea v-model="body" id="createbodyinput" cols="30" rows="10" placeholder="Text" required></textarea>
                        <div id="buttonholders">
                            <input id="cancelbutton" type="button" value="Cancel" v-on:click='closepostform'>
                            <input id="postbutton" type="button" value="Create Post" v-on:click='createpost'>
                        </div>
                        <h1 id='errormessage' style="display: none;"></h1>
                    </form>
                </div>
            </div>
        </div>
        <div id ='moreposts'>
            <button id="morepostsbutton" v-on:click="loadmoreposts">Load more posts</button>
        </div>
    </div>
</template>

<script>
import postroutes from '../posts.js'
import dateago from '../dateago.js'

export default {
    name: 'homecomponent',
    data() {
        return {
            posts: [],
            pagenum: 1,
            title: '',
            body: ''
        }
    },
    async created() {
        try {
            this.posts = await postroutes.getnew(this.pagenum)
            var i
            var newdiv
            for(i = 0; i < this.posts.length; i++) {
                newdiv = document.createElement('div')
                newdiv.className = 'postcontainer'
                newdiv.id = `${this.posts[i]._id}`
                
                var likebuttonandcommentnumdiv = document.createElement('div')
                likebuttonandcommentnumdiv.id = 'likeandcomment'
                
                var likenum = document.createElement('h5')
                likenum.id = 'likenum'
                likenum.innerHTML = `Likes: ${this.posts[i].like_count}`
                
                var commentnum = document.createElement('h5')
                commentnum.id = 'commentnum'
                commentnum.innerHTML = `Comments: ${this.posts[i].comments.length}`
                likebuttonandcommentnumdiv.appendChild(likenum)
                likebuttonandcommentnumdiv.appendChild(commentnum)

                var linktopost = document.createElement('a')
                linktopost.id = 'linktopost'
                linktopost.href = `/${this.posts[i]._id}`
                
                var posttitle = document.createElement('textarea')
                posttitle.className = 'posttitle'
                posttitle.cols = '50'
                posttitle.rows = '2'
                posttitle.disabled = true
                posttitle.innerHTML = (`${this.posts[i].title}`)
                
                var userlabel = document.createElement('h5')
                userlabel.className = 'userlabel'
                
                var datetime = dateago.finddateago(this.posts[i].createdAt.getTime())
                userlabel.innerHTML = `Posted by ${this.posts[i].username} ${datetime} ago`

                newdiv.appendChild(userlabel)
                linktopost.appendChild(posttitle)
                newdiv.appendChild(linktopost)
                newdiv.appendChild(likebuttonandcommentnumdiv)
                document.getElementById("home").appendChild(newdiv)
            }
            document.getElementById('newbutton').disabled = true
        } catch(err) {
            console.log(err)
            this.$router.push({path: '/login'})
        }
    },

    methods: {
    async displaynew() {
        [...document.getElementsByClassName("postcontainer")].map(n => n && n.remove());
        document.getElementById('newbutton').disabled = true
        document.getElementById('popbutton').disabled = false
        try {
            this.pagenum = 1
            this.posts = await postroutes.getnew(this.pagenum)
            var i
            var newdiv
            for(i = 0; i < this.posts.length; i++) {
               newdiv = document.createElement('div')
                newdiv.className = 'postcontainer'
                newdiv.id = `${this.posts[i]._id}`
                
                var likebuttonandcommentnumdiv = document.createElement('div')
                likebuttonandcommentnumdiv.id = 'likeandcomment'
                
                var likenum = document.createElement('h5')
                likenum.id = 'likenum'
                likenum.innerHTML = `Likes: ${this.posts[i].like_count}`
                
                var commentnum = document.createElement('h5')
                commentnum.id = 'commentnum'
                commentnum.innerHTML = `Comments: ${this.posts[i].comments.length}`
                likebuttonandcommentnumdiv.appendChild(likenum)
                likebuttonandcommentnumdiv.appendChild(commentnum)

                var linktopost = document.createElement('a')
                linktopost.id = 'linktopost'
                linktopost.href = `/${this.posts[i]._id}`
                
                var posttitle = document.createElement('textarea')
                posttitle.className = 'posttitle'
                posttitle.cols = '50'
                posttitle.rows = '2'
                posttitle.disabled = true
                posttitle.innerHTML = (`${this.posts[i].title}`)
                
                var userlabel = document.createElement('h5')
                userlabel.className = 'userlabel'
                
                var datetime = dateago.finddateago(this.posts[i].createdAt.getTime())
                userlabel.innerHTML = `Posted by ${this.posts[i].username} ${datetime} ago`

                newdiv.appendChild(userlabel)
                linktopost.appendChild(posttitle)
                newdiv.appendChild(linktopost)
                newdiv.appendChild(likebuttonandcommentnumdiv)
                document.getElementById("home").appendChild(newdiv)
            }
        } catch(err) {
            console.log(err)
        }
    },
    async displaypop() {
        [...document.getElementsByClassName("postcontainer")].map(n => n && n.remove());
        document.getElementById('popbutton').disabled = true
        document.getElementById('newbutton').disabled = false
        try {
            this.pagenum = 1
            this.posts = await postroutes.getpopular(this.pagenum)
            var i
            var newdiv
            for(i = 0; i < this.posts.length; i++) {
                newdiv = document.createElement('div')
                newdiv.className = 'postcontainer'
                newdiv.id = `${this.posts[i]._id}`
                
                var likebuttonandcommentnumdiv = document.createElement('div')
                likebuttonandcommentnumdiv.id = 'likeandcomment'
                
                var likenum = document.createElement('h5')
                likenum.id = 'likenum'
                likenum.innerHTML = `Likes: ${this.posts[i].like_count}`
                
                var commentnum = document.createElement('h5')
                commentnum.id = 'commentnum'
                commentnum.innerHTML = `Comments: ${this.posts[i].comments.length}`
                likebuttonandcommentnumdiv.appendChild(likenum)
                likebuttonandcommentnumdiv.appendChild(commentnum)

                var linktopost = document.createElement('a')
                linktopost.id = 'linktopost'
                linktopost.href = `/${this.posts[i]._id}`
                
                var posttitle = document.createElement('textarea')
                posttitle.className = 'posttitle'
                posttitle.cols = '50'
                posttitle.rows = '2'
                posttitle.disabled = true
                posttitle.innerHTML = (`${this.posts[i].title}`)
                
                var userlabel = document.createElement('h5')
                userlabel.className = 'userlabel'
                
                var datetime = dateago.finddateago(this.posts[i].createdAt.getTime())
                userlabel.innerHTML = `Posted by ${this.posts[i].username} ${datetime} ago`

                newdiv.appendChild(userlabel)
                linktopost.appendChild(posttitle)
                newdiv.appendChild(linktopost)
                newdiv.appendChild(likebuttonandcommentnumdiv)
                document.getElementById("home").appendChild(newdiv)
            }
        } catch(err) {
            console.log(err)
        }
    },

    movetopost(postId) {
        console.log(postId)
    },

    async loadmoreposts() {
        try {
            this.pagenum++
            if(document.getElementById('newbutton').disabled == true) this.posts = await postroutes.getnew(this.pagenum)
            if(document.getElementById('newbutton').disabled == false) this.posts = await postroutes.getpopular(this.pagenum)
            var i
            var newdiv
            for(i = 0; i < this.posts.length; i++) {
               newdiv = document.createElement('div')
                newdiv.className = 'postcontainer'
                newdiv.id = `${this.posts[i]._id}`
                
                var likebuttonandcommentnumdiv = document.createElement('div')
                likebuttonandcommentnumdiv.id = 'likeandcomment'
                
                var likenum = document.createElement('h5')
                likenum.id = 'likenum'
                likenum.innerHTML = `Likes: ${this.posts[i].like_count}`
                
                var commentnum = document.createElement('h5')
                commentnum.id = 'commentnum'
                commentnum.innerHTML = `Comments: ${this.posts[i].comments.length}`
                likebuttonandcommentnumdiv.appendChild(likenum)
                likebuttonandcommentnumdiv.appendChild(commentnum)

                var linktopost = document.createElement('a')
                linktopost.id = 'linktopost'
                linktopost.href = `/${this.posts[i]._id}`
                
                var posttitle = document.createElement('textarea')
                posttitle.className = 'posttitle'
                posttitle.cols = '50'
                posttitle.rows = '2'
                posttitle.disabled = true
                posttitle.innerHTML = (`${this.posts[i].title}`)
                
                var userlabel = document.createElement('h5')
                userlabel.className = 'userlabel'
                
                var datetime = dateago.finddateago(this.posts[i].createdAt.getTime())
                userlabel.innerHTML = `Posted by ${this.posts[i].username} ${datetime} ago`

                newdiv.appendChild(userlabel)
                linktopost.appendChild(posttitle)
                newdiv.appendChild(linktopost)
                newdiv.appendChild(likebuttonandcommentnumdiv)
                document.getElementById("home").appendChild(newdiv)
            }
        } catch(err) {
            console.log(err)
        }
    },

    createpostform() {
        document.getElementById('overlay').style.display = "block"
    },

    closepostform() {
        document.getElementById('overlay').style.display = "none"
        document.getElementById('createpostbox').style.height = '400px'
        document.getElementById('errormessage').style.display = 'none'
    },

    async createpost() {
        try {
            await postroutes.createpost(this.title, this.body)
            this.closepostform()
            this.displaynew()
            
        } catch(err) {
            var postbox = document.getElementById('createpostbox')
            postbox.style.height = '450px'
            var errormessage = document.getElementById('errormessage')
            errormessage.innerHTML = err.response.data.message
            errormessage.style.display = 'block'
        }
        
    }
    
    }
}
</script>

<style>
#home {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#sortby {
    display: flex;
    justify-content: left;
    width: 640px;
    height: 22px;
    margin-left: 10px;
}

#optionsholder {
    display: flex;
    justify-content:left;
    height: 50px;
    width: 640px;
}

.orderbutton {
    width: 62px;
    margin-right: 5px;
    margin-top: 5px;
    height: 40px;
    border-radius: 12px;
    border-color: beige;
    background-color: rgba(81, 80, 82, 0.3);
    font-size: 13px;
    cursor: pointer;
}

.orderbutton:disabled {
    background-color: rgba(245, 245, 245, 0.3);
    color: black;
}

#createpostbutton {
    height: 50px;
    width: 200px;
    border-radius: 5px;
    border-color: beige;
    background-color:rgba(189, 114, 199, 0.541);
    margin-left:340px;
    cursor: pointer;
}

#overlay {
  position: fixed;
  display: none;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
  cursor: pointer;
}

#formholder {
    display: flex;
    justify-content: center;
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 110;
}

#createpostbox {
    box-shadow: 1px 7px 20px 2px rgb(0 0 0 / 40%);
    border-radius: 10px;
    position: fixed;
    display: flex;
    align-self: center;
    flex-direction: column;
    justify-content: flex-start;
    background-color:rgb(228, 225, 225);
    height: 400px;
    width: 500px;
}

#newpostlabel {
    text-align: left;
    margin: 0%;
    margin-left: 20px;
    margin-top: 10px;
}

#createtitleinput {
    width: 450px;
    height: 40px;
    margin-top: 10px;
    margin-left: 20px;
    font-size: 20px;
}

#createbodyinput {
    width: 450px;
    height: 250px;
    margin-top: 5px;
    margin-left: 20px;
    font-size: 16px;
    resize: none;
}

#createbodyinput::-webkit-scrollbar {
    width: 10px;
}

#createbodyinput::-webkit-scrollbar-track {
    box-shadow: 0ch;
    -webkit-box-shadow: inset 0 0 6px rgba(174, 0, 255, 0.5);
}

#createbodyinput::-webkit-scrollbar-thumb {
  background-color:  rgba(174, 0, 255, 0.3);

}

#cancelbutton {
    width: 120px;
    height: 50px;
    margin-right: 10px;
    border-radius: 5px;
    border-color: beige;
    background-color: rgb(228, 225, 225);
}

#postbutton {
    width: 120px;
    height: 50px;
    margin-left: 10px;
    border-radius: 5px;
    border-color: rgb(220, 233, 245);
    background-color: rgba(189, 114, 199, 0.541);
}

.postcontainer {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin: 10px 8px;
    width: 40rem;
    height: 100px;
    background-color: rgba(106, 25, 143, 0.2);
    border-radius: 10px;
    box-shadow: 1px 7px 10px 2px rgb(0 0 0 / 40%);
}

.linktopost {
    
    
    height: 50px;
    padding: 0%;
    margin: 0%;
}

.posttitle {  
    color:rgb(0, 0, 0);
    margin: 10px;
    margin-top: 0%;
    margin-bottom: 0%;
    font-size: 20px;
    background: none;
    border: none;
    resize: none;
    cursor: pointer;
}

#userlabel {
    color:rgb(95, 95, 95);
    
    margin-top: 10px;
    margin-left: 20px;
}

#errormessage {
    color: rgb(143, 13, 13);
    font-size: 24px;
    font: bold;
}

#likeandcomment {
    display: flex;
    flex-direction: row;
}

#likenum {
    margin: 0%;
    margin-left: 12px;
}

#commentnum {
    margin: 0%;
    margin-left: 30px;
}
</style>