<template>
    <div>
        <div class = 'login'>
            <h1 id="pleaselogin">Please login</h1>
            <h2 id='errorlogin' style="display: none"></h2>
            <input type="text" id="loginusername" v-model="username" placeholder="username">
            <input type="password" id="loginpassword" v-model="password" placeholder="password">
            <button v-on:click='login'>Login</button>
        </div>
        <div>
            <label for="signupbutton">Don't have an account?</label>
            <button id='signupbutton' v-on:click='signupform'>Sign Up</button>
        </div>
        <div id='overlay' style="display: none;">
            <div id='formholder'>
                <form id='signupbox'>
                    <label for="signupinputusername" id= 'signuplabel'>Enter Username</label>
                    <input type="text" id="signupinputusername" v-model="username" placeholder="username">
                    <label for="signupinputpassword" id= 'signuplabel'>Enter Password</label>
                    <input type="password" id="signupinputpassword" v-model="password" placeholder="password">
                    <input type="button" value="Sign Up" v-on:click="signup">
                    <h1 id='resulttext'></h1>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import loginroutes from '../login.js'

export default {
    name: 'logincomponent',
    data() {
        return {
            username: '',
            password: ''
        }
    },

    created() {
        document.getElementById('useraccountandlogout').style.display = 'none'
    },
    methods: {
        login() {
            loginroutes.login(this.username, this.password).then((result) => {
                if(result === 'success') this.$router.push({path: '/'})
            }).catch((err) => {
                console.log(err.response.data.message)
                document.getElementById('errorlogin').innerHTML = `${err.response.data.message}`
                document.getElementById('errorlogin').style.display = "block"
            })
        },
        signupform() {
            if(document.getElementById('overlay').style.display == "none") {
                document.getElementById('resulttext').innerHTML = ''
                document.getElementById('overlay').style.display = "block"
            } else {
                document.getElementById('overlay').style.display = "none"
            }
            
        },
        signup() {
            loginroutes.signup(this.username, this.password).then((result) => {
                if(result.status == 200) {
                    setTimeout(() => {
                        document.getElementById('overlay').style.display = "none"
                    }, 1500);
                }
            }).catch((err) => {
                console.log(err.response.data.message)
                document.getElementById('resulttext').innerHTML = `${err.response.data.message}`
            })
        }
    }
}
</script>

<style>

#pleaselogin {
    margin-bottom: 0%;
    color: black;
}

#errorlogin {
    margin-bottom: 0%;
    color: rgb(143, 13, 13);
}

.login {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#login {
    width: 8vw;
}

#signupbutton {
    background:none;
    border:none;
    color: blueviolet;
}

#overlay {
  position: fixed;
  display: none;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
  cursor: pointer;

}

#formholder {
    display: flex;
    justify-content: center;
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 110;
}

#signupbox {
    box-shadow: 1px 7px 20px 2px rgb(0 0 0 / 40%);
    border-radius: 10px;
    position: fixed;
    display: flex;
    align-self: center;
    flex-direction: column;
    justify-content: center;
    background-color:rgb(232, 232, 241);
    height: 300px;
    width: 500px;
}


#signuplabel {
    display: flex;
    align-self: left;
    padding-left: 10px;
    width: 120px;
}

#signupinput {
    display: flex;
    align-self: left;
    position: relative;
    width: 120px;
    text-shadow: 0 -1px 0 rgba(0,0,0,0.2), 0 1px 0 #fff;
    margin-left: 10px;
}

#resulttext {
    color: rgb(143, 13, 13);
}

</style>